import React from 'react';
import { useComplexLanguageMethod } from '../../../contexts/languageContext';

const Intro = ({ header, subHeader }) => {
  const { t } = useComplexLanguageMethod();
  return (
    <>
      <h1>{t(header)}</h1>
      <div className='intro-section'>
        <h2>{t(subHeader)}</h2>
        <section>
          <p className='online'>
            Online
            <br />
            Life Tech connected
          </p>
          <p className='offline'>
            Offline
            <br />
            Normal bio life
          </p>
        </section>
      </div>
    </>
  );
};

export default Intro;
